.review-page {
  font-size: 16px;
  margin-bottom: 4px; }

.review-title {
  font-family: Pacifico;
  font-size: 3em;
  line-height: 1.5;
  text-align: center;
  color: #F17298;
  margin-bottom: 32px;
  padding: 0 15px; }

.review-block {
  padding: 80px 0;
  background: #FFC850;
  position: relative;
  padding-bottom: 56px; }
  .review-block .top_box_left,
  .review-block .top_box_right,
  .review-block .bot_box_left,
  .review-block .bot_box_right {
    width: 0px;
    height: 0px; }
  .review-block .top_box_right {
    border-style: solid;
    border-width: 0 1425px 50px 0;
    border-color: transparent #ffffff transparent transparent;
    position: absolute;
    top: 0; }
  .review-block .top_box_left {
    border-style: solid;
    border-width: 50px 0 0px 1439px;
    border-color: transparent transparent transparent #ffffff;
    position: absolute;
    top: 0; }
  .review-block .bot_box_right {
    border-style: solid;
    border-width: 0 1425px 100px 0;
    border-color: transparent transparent #ffffff transparent;
    position: absolute;
    bottom: 0; }
  .review-block-content {
    padding: 8px;
    background: #ffffff;
    border-radius: 5px;
    max-width: 589px;
    margin-left: auto;
    margin-bottom: 24px; }
  .review-block_img {
    position: relative;
    margin-bottom: 16px; }
    .review-block_img .placeholder {
      width: 100%; }
    .review-block_img .trigger {
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      cursor: pointer; }
  .review-block_des {
    max-width: 519px;
    margin: auto; }
    .review-block_des p {
      line-height: 23px;
      font-size: 17px;
      text-align: center;
      color: #F17298; }
  .review-block-quote {
    position: relative;
    background: #ffffff;
    border-radius: 5px;
    max-width: 589px;
    padding: 80px;
    margin-bottom: 24px; }
    .review-block-quote_icon {
      position: absolute;
      right: 20px;
      top: -30px; }
    .review-block-quote_des strong {
      color: #F17298; }
  .review-block_video_1 {
    padding-bottom: 50px;
    background: #FFC850; }
  .review-block_video_2 {
    padding-bottom: 40px;
    padding-top: 24px;
    background: #ECB7CB; }
    .review-block_video_2 .top_box_left {
      border-color: transparent transparent #ECB7CB transparent;
      border-width: 0 0 80px 1425px;
      margin-top: -80px; }
  .review-block_comment_1 {
    padding-bottom: 50px;
    padding-top: 40px;
    background: #92D3D1; }
    .review-block_comment_1 .top_box_right {
      border-color: transparent transparent #92D3D1 transparent;
      border-width: 0 1425px 80px 0px;
      margin-top: -80px; }
  .review-block_comment_2 {
    padding-bottom: 50px;
    padding-top: 24px;
    background: #FECAAD; }
    .review-block_comment_2 .top_box_left {
      border-color: transparent transparent #FECAAD transparent;
      border-width: 0 0 80px 1425px;
      margin-top: -80px; }

@media (max-width: 991px) {
  .review-block_video_1 {
    padding-bottom: 32px; }
    .review-block_video_1 .review-block-content {
      margin: 24px auto; }
    .review-block_video_1 .review-block_modal {
      text-align: right; }
  .review-block_comment_1 {
    padding-bottom: 40px; }
  .review-block_video_2 .col-lg-6.position-relative, .review-block_comment_2 .col-lg-6.position-relative {
    order: 2; } }

@media (max-width: 768px) {
  .review-page {
    font-size: 14px; }
  .review-block_video_1 {
    padding-bottom: 14px; }
  .review-block_video_2 {
    padding-bottom: 24px; }
    .review-block_video_2 .top_box_left {
      border-width: 0 0 40px 1425px;
      margin-top: -39px; }
  .review-block_comment_1 {
    padding-bottom: 8px; }
  .review-block_comment_2 .top_box_left {
    border-width: 0 0 40px 1425px;
    margin-top: -39px; } }

@media (max-width: 425px) {
  .review-block-quote {
    padding: 32px 24px; } }
